@tailwind base;
@tailwind components;
@tailwind utilities;

@import url('https://fonts.googleapis.com/css2?family=Open+Sans:wght@400;500;600;700;800&display=swap');

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  scroll-behavior: smooth;
}

body {
  margin: 0;
  /* font-family: 'Open Sans', BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif; */
    font-family: 'Open Sans', sans-serif !important;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #2e2e35;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

i {
  cursor: pointer !important;
}

h1,h2,h3,h4,h5,h6 {
  color: #dadadb;
}

p {
  color: #dadadb;
  font-weight: 500;
}

span {
  color: #e4aa49;
  font-weight: bold;
}

li {
  cursor: pointer;
}
/* div { 
  border: 1px solid #fbbf24;
} */
@layer components {
  .social-hoof {
    @apply
    relative
    transition-all
    duration-100
    animate-spin

  }
}
@layer components {
  .u-a {
    @apply capitalize
    inline-block
    relative
    cursor-pointer
    transition-all
    duration-500
    before:content-['']
    before:absolute
    before:-bottom-1
    before:left-0
    before:w-0
    before:h-[2px]
    before:rounded-full
    before:opacity-0
    before:transition-all
    before:duration-500
    before:bg-gradient-to-r
    before:from-amber-400
    before:via-amber-400
    before:to-amber-400
    hover:before:w-full
    hover:before:opacity-100
  }
}